.select {
    margin-left: 10px;
    display: flex;
    justify-content: space-between;
    width: 100px;
    height: 20px;
    line-height: 20px;
    border: 2px solid grey;
    border-radius: 5px;
    padding: 5px;
}

.arrowUp,
.arrowDown {
    width: 15px;
    height: 15px;
    border: 2px solid grey;
    border-radius: 50%;
    position: relative;
    transition: 0.25s;
    &:hover{
        cursor: pointer;
    }
    &::after {
        content: '>';
        position: absolute;
        top: -30%;
        left: 20%;
        color: gray;
    }
}
.arrowUp {
    transform: rotate(90deg);
}
.arrowDown {
    transform: rotate(-90deg);
}

.selectBody {
    position: absolute; /* для того, чтобы selectBody не опускало нижний контент, а накладывалось на него*/
    margin: 2px 0 0 10px;
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100px;
    height: 75px;
    overflow: auto;
    border: 2px solid grey;
    border-radius: 5px;
    padding: 5px;
    &:hover{
        cursor: pointer;
    }
    &::-webkit-scrollbar {
        width: 4px;
        border-radius: 4px;
    }
    &::-webkit-scrollbar-track {
        background-color: gray;
        border-radius: 4px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: lightgrey;
        border-left: 2px solid gray;
        border-top: 2px solid gray;
        border-bottom: 2px solid gray;
        height: 20px;
        border-radius: 4px;

    }
}

.item {
    background-color: lightblue;
}
